import quickBtn from '@/components/mbbo/quickBtn/quickBtn.vue';
import navigation from '@/components/mbbo/navigation/navigation.vue';
import { mapMutations, mapGetters } from 'vuex';
export default {
	data() {
		return {
			weixuan:  this.Static+'my/15.png',
			xuanzhong:  this.Static+'my/16.png',
			bot:  this.Static+'my/bot.png',
			list:[],
			add:[],
		};
	},
	components: {
		quickBtn,
		navigation,
	},
	computed: {
		i18n() {
			return this.$t('Mypersonal');
		},
		...mapGetters(['GET_HOME', 'QUICKNAVCO','GET_MY'])
	},
	watch: {
		GET_MY: {
			handler(a, b) {
				// console.log(a,b)
				this.add=a.address;
			},
			deep: true
		}
	},
	created() {
		this.address();
	},
	methods: {
		xuanadd(e){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				// console.log(landRegistLG.user.id);
				let params = {
					"addrState":1,
					//用户ID
					"userId":landRegistLG.user.id,
					//传递就编辑 不传递就插入
					"id":e,
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/address/', //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						// console.log(response.data);
						this.address();
						this.$emit('tap-XuanZeAddress', true);
						// // console.log(this.list)
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						// console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		gotoaddressnew(){
			// console.log('添加地址');
			uni.navigateTo({
				url: '/modules/myCompany/mySetup/mySetups/mySetup-address-new',
			});
		},
		xiuadd(e){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				// console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/address/'+e, //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						// console.log(response.data);
						this.list=response.data.content;
						this.$store.commit('setress', this.list); // 更新vuex
						uni.navigateTo({
							url: '/modules/myCompany/mySetup/mySetups/mySetup-address-new2?id='+e,
						});
						// // console.log(this.list)
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						// console.log(error, '网络繁忙，请稍后');
					}
				});
			}
			// console.log('编辑地址');
			
		},
		deladd(e){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				// console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/address/del/' + e, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						// console.log(response.data);
						// // console.log(this.list)
						this.address();
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						// console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		address(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				// console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/address?userId='+landRegistLG.user.id, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						// console.log(response.data);
						this.list = response.data.content
						// // console.log(this.list)
						this.$store.commit('setaddress', this.list); // 更新vuex
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						// console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
};
